
export default {
	onLaunch: function() {

























































		const s = this.$store
		if (s.getters.token) {
			// 发起登录请求
			s.dispatch('Logins')
		} else {
			uni.login({
				provider: 'weixin',
				success: res => {
					if (res.code) {
						// 发起登录请求
						s.dispatch('Login', {
							code: res.code.trim(),
						})
					} else {
						uni.showToast({
							title: '微信登录接口调用失败，请联系管理员修复',
							icon: 'none',
							duration: 2000
						})
					}
				}
			});
		}

	}
}
